$color: black;
$bgColor: rgba(199, 247, 227, 0.6);

/*-----------GENERAL-------------*/

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  color: $color;
  font-size: 20px;
  background-color: $bgColor;
}

header .logo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 10px;
}

header {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid darkolivegreen;
  // position: fixed;
  // width: 100%;
  // top: 0;
  // padding: 10px;
  // z-index: 1;
}

h1, .subtitle {
  margin: 0;
  text-align: center;
  line-height: .97em;
}

/*-----------MOVIE VIEW-------------*/

img {
  height: 175px;
}

.movieviewContent img {
  height: 500px;
}

.cardContainer img {
  height: 250px;
}

.movieviewContent {
  display: flex;
  align-items: center;
  // text-align: justify;
  // width: 75%;
  margin: 50px;
}

.movieviewInfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 50px;
  font-size: 18px;
  line-height: 1.2em;
}

.movieviewInfo div:nth-child(odd) {
  margin: 12px 0;
}

.movieViewContainer h2 {
  margin: 0;
  // text-align: center;
}

.movieviewContent span:first-of-type, .movieviewInfo strong {
  font-weight: bold;
  font-size: 20px;
}

.movieCard__year {
  font-style: italic;
  font-weight: 100;
  font-size: 0.8em;    
}

.movieCard {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 250px;
  border: 2px solid darkolivegreen;
  border-radius: 5px;
  // margin: 10px;
  padding: 10px 5px;
  text-align: center;
  cursor: pointer;
  background-color: rgba(184, 181, 181, 0.5);
  font-weight: bold;
}

.similarMovies .movieCard {
  width: 150px;
  margin: 0;
}

.cardContainer {
  display: flex;
  flex-wrap: wrap;
  line-height: .95em;
  margin: 50px;
  justify-content: center;
  gap: 10px;
}

.similarMovies {
  display: flex;
  flex-wrap: wrap;
  line-height: .95em;
  justify-content: center;
  overflow: auto;
  // margin: 50px;
  margin-bottom: 10px;
}

.similarMoviesContainer h2 {
  border-top: 2px solid darkolivegreen;

}

.movieCard:hover {
  background-color: rgba(184, 181, 181, 0.8);
}

.movieViewContainer button, button {
  margin: 10px;
  width: 200px;
  height: 30px;
  border-radius: 5px;
  align-self: center;
}

/* ------------MEDIA QUERIES---------------*/
@media (max-width: 1000px) {
  .movieviewContent {
    flex-direction: column;
  }

}
